.tag-item
    position: relative
    &:hover
        .tag-cover
            filter: blur(0)
        //.tag-link
        //    align-items: end
.tag-cover
    border-radius: 5px
    padding: 5px 10px
    filter: blur(5px)
    transition: filter .3s linear
    height: 200px
    background-size: cover
    background-repeat: no-repeat
    background-position: center
.tag-text
    position: absolute
    top: 0
    height: 100%
    width: 100%
.tag-link
    height: 100%
    width: 100%
    display: flex
    justify-content: center
    align-items: center
    color: $tc-white
.tag-link-text
    //display: inline-block
    //position: absolute
    //top: 50%
    //left: 50%
    //transform: translate(-50%, -50%)
    background-color: rgba($tc-blue-dark-3, 0.5)
    padding: 5px 10px
    border-radius: 5px

